<template>
  <div class="modal" tabindex="-1" ref="modal">
  <div class="modal-dialog modal-dialog-centered modal-lg">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title"></h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        <!--  -->
        <SetCropImg ref="setCropImg"></SetCropImg>
        <!--  -->
        <CropImg :imgUrl="imgUrl" ref="cropImg"></CropImg>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
        <button type="button" class="btn btn-primary d-none" ref="confirmCrop" @click="confirmCrop"></button>
      </div>
    </div>
  </div>
</div>

</template>

<script>
import Modal from 'bootstrap/js/dist/modal.js'
import CropImg from '@/components/upload/function/CropImg.vue' //* 
import SetCropImg from '@/components/upload/set/SetCropImg.vue'
export default {
  components: {
    CropImg,
    SetCropImg
  },

  data () {
    return {
      modal: '',
      imgUrl: '',
      currentImgIndex: -1
    }
  },

  methods: {
    openModal (imgUrl, index) {
      this.currentImgIndex = index
      this.imgUrl = imgUrl
      this.modal.show()
    },
    confirmCrop () {
      const cropImgUrl = this.$refs.cropImg.getCropUrl()
      //* 
      this.$parent.imgUrlArr[this.currentImgIndex].url = cropImgUrl
      //*  file
      this.$parent.$parent.file[this.currentImgIndex].cropImgUrl = cropImgUrl

      this.$refs.confirmCrop.classList.add('d-none')
      this.modal.hide()
    }
  },

  mounted () {
    this.modal = new Modal(this.$refs.modal)
    //*  Modal 
    this.$refs.modal.addEventListener('hidden.bs.modal', () => {
      //* Modal 
      this.$refs.cropImg.cropImgUrl = ''
      //* 
      this.$refs.cropImg.option.autoCropWidth = 160
      this.$refs.cropImg.option.autoCropHeight = 150
      //*  
      this.$refs.setCropImg.crop.width = 160
      this.$refs.setCropImg.crop.height = 150
    })
  }

}
</script>
