<template>
  <uploader :options="options" class="uploader-example" :autoStart="true" ref="uploader"
    @file-added="onFileAdded"
    @file-success="onFileSuccess"
    @file-progress="onFileProgress"
    @file-error="onFileError">
    <uploader-unsupport></uploader-unsupport>
    <uploader-drop>
      <p></p>
      <uploader-btn></uploader-btn>
      <!-- <uploader-btn v-if="uploadMode===''" :attrs="attrs"></uploader-btn> -->
      <!-- <uploader-btn :directory="true"></uploader-btn> -->
    </uploader-drop>
    <uploader-list ref="uploaderList"></uploader-list>
  </uploader>

  <!--  -->
  <div class="my-3">
    <p ref="feedback"></p>
  </div>

  <!--  -->
  <PreviewImg :file="file" :mode="setup.mode" :singleFile="singleFile"></PreviewImg>

  <!--  -->
  <div class="text-center my-2" v-if="Object.keys(file).length > 0">
    <button type="button" class="btn btn-primary" @click="$emit('getFormData', file)"></button>
  </div>
</template>

<script>
import PreviewImg from '@/components/upload/function/PreviewImg.vue'
import SparkMD5 from 'spark-md5'
import heic2any from 'heic2any'
export default {

  components: {
    PreviewImg
  },

  emits: ['getFormData'],

  props: {
    setup: {
      type: Object
    }
  },

  data () {
    return {
      file: [], //*  file
      options: {
        // target: '//localhost:3000/upload', // '//jsonplaceholder.typicode.com/posts/',
        target: '/', // 
        testChunks: true,
        chunkSize: '2048000', // 
        maxChunkRetries: 3, // 
        singleFile: this.setup.singleFile //* 
      },
      attrs: {
        accept: 'image/*'
      },
      statusText: {
        success: '',
        error: '',
        uploading: '',
        paused: '',
        waiting: ''
      },
      uploadStatus: {
        null: 0,
        success: 1,
        fail: 2
      }
    }
  },

  methods: {
    async onFileAdded (file) {
      const type = file.name.split('.').pop()
      //* 
      const validateType = this.setup.validateType
      if (!validateType.includes(type)) {
        file.cancel()
        file.ignored = true
        const err = `${type}`
        this.failFeedback(err)
        throw err
      }
      //* 
      if (this.setup.size.hasValidateSize) {
        const size = file.size
        if (size > this.setup.size.validateSize) {
          file.cancel()
          file.ignored = true
          const err = ` ${this.setup.size.validateSize / 1024} Kb `
          this.failFeedback(err)
          throw (err)
        }
      }
      //* 
      if (this.setup.mode === 'img') {
        //* heic 
        if (type === 'heic' || type === 'heif') {
          await this.heic2Jpeg(file.file).then(url => {
            const img = new Image()
            img.src = url
            if (this.setup.hasValidateResolution) {
              img.onload = () => {
                if (img.width > this.setup.validateW || img.height > this.setup.validateH) {
                  file.cancel()
                  file.ignored = true
                  this.removeValidateFailFile(file)
                  const err = ` ${this.setup.validateW}*${this.setup.validateH} ${img.width}*${img.height}`
                  this.failFeedback(err)
                  throw err
                }
              }
            }
          })
        } else { //* 
          await this.getImgSize(file)
            .then(img => {
              if (this.setup.hasValidateResolution) {
                if (img.width > this.setup.validateW || img.height > this.setup.validateH) {
                  file.cancel()
                  this.removeValidateFailFile(file)
                  const err = ` ${this.setup.validateW}*${this.setup.validateH} ${img.width}*${img.height}`
                  this.failFeedback(err)
                  throw err
                }
              }
            })
        }
      }

      //* 
      this.successFeedback()
      this.panelShow = true
      this.computeMD5(file)
      file.params = this.params
    },
    getImgSize (file) {
      return new Promise((resolve, reject) => {
        const url = URL.createObjectURL(file.file)
        const img = new Image()
        img.src = url
        img.onload = function () {
          const imgResolution = {}
          imgResolution.width = img.naturalWidth
          imgResolution.height = img.naturalHeight
          resolve(imgResolution)
        }
      })
    },
    //*  heic  heif  JPG
    heic2Jpeg (file) {
      return new Promise((resolve, reject) => {
        const name = file.name
        heic2any({
          blob: file,
          toType: 'image/jpeg',
          quality: 1
        }).then((heicToJpgResult) => {
          const url = URL.createObjectURL(heicToJpgResult)
          //*  Heic Url
          const obj = this.$refs.uploaderList.fileList.filter(files => {
            return files.name === file.name
          })
          const newObj = obj[0]
          newObj.heic = true //* Heic  previewImg 
          newObj.url = url
          newObj.name = name
          newObj.heic2Jpeg = heicToJpgResult

          //*  heic  jepg  file
          this.file.push(newObj)

          resolve(url)
        })
      })
    },
    //* 
    removeValidateFailFile (file) {
      const fileName = file.name
      const removeFileIndex = this.file.findIndex(file => {
        return file.name === fileName
      })
      this.file.splice(removeFileIndex, 1)
    },
    onFileProgress (rootFile, file, chunk) {
      console.log(` ${file.name}chunk${chunk.startByte / 1024 / 1024} ~ ${chunk.endByte / 1024 / 1024}`)
    },
    onFileSuccess (rootFile, file, response, chunk) {
      const isHeic = 'heic'
      //* heic  heic2Jpeg  file
      //* ()
      if (file.name.includes(isHeic)) return

      //*  file
      if (!file.aborted) {
        this.file.push(file)
      }

      //* 
      if (this.options.singleFile) {
        this.file = []
        this.file.push(file)
      }
    },
    onFileError (rootFile, file, response, chunk) {
      this.$message({
        message: response,
        type: 'error'
      })
    },
    //* md5
    computeMD5 (file) {
      const fileReader = new FileReader()
      const time = new Date().getTime()
      const blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice
      let currentChunk = 0
      const chunkSize = 10 * 1024 * 1000
      const chunks = Math.ceil(file.size / chunkSize)
      const spark = new SparkMD5.ArrayBuffer()
      file.pause()
      loadNext()
      fileReader.onload = e => {
        spark.append(e.target.result)
        if (currentChunk < chunks) {
          currentChunk++
          loadNext()
        } else {
          const md5 = spark.end()
          this.computeMD5Success(md5, file)
          console.log(`MD5${file.name} \nMD5${md5} \n${chunks} :${file.size} ${new Date().getTime() - time} ms`)
        }
      }
      fileReader.onerror = function () {
        this.error(`${file.name}`)
        file.cancel()
      }
      function loadNext () {
        const start = currentChunk * chunkSize
        const end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize
        fileReader.readAsArrayBuffer(blobSlice.call(file.file, start, end))
      }
    },
    computeMD5Success (md5, file) {
      file.uniqueIdentifier = md5
      file.resume()
    },
    close () {
      this.uploader.cancel()
      this.panelShow = false
    },
    error (msg) {
      this.$notify({
        title: '',
        message: msg,
        type: 'error',
        duration: 2000
      })
    },
    //* 
    failFeedback (content) {
      this.status = this.uploadStatus.fail
      this.$refs.feedback.textContent = content
      this.$refs.feedback.className = 'fail text-danger fst-italic'
    },
    successFeedback (e) {
      this.status = this.uploadStatus.success
      this.$refs.feedback.textContent = ''
      this.$refs.feedback.className = 'success text-success'
    }
  }

}
</script>

<style>
  .uploader-example {
    width: 880px;
    padding: 15px;
    margin: 40px auto 0;
    font-size: 12px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .4);
  }
  .uploader-example .uploader-btn {
    margin-right: 4px;
  }
  .uploader-example .uploader-list {
    max-height: 440px;
    overflow: auto;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>
