<template>
    <div class="row height my-3">
      <div class="col-5">
        <VueCropper
          ref="cropper"
          :img="imgUrl"
          :outputSize="option.size"
          :outputType="option.outputType"
          :info="true"
          :full="option.full"
          :fixed="option.fixed"
          :fixedNumber="option.fixedNumber"
          :can-move="option.canMove"
          :can-move-box="option.canMoveBox"
          :fixed-box="option.fixedBox"
          :original="option.original"
          :auto-crop="option.autoCrop"
          :auto-crop-width="option.autoCropWidth"
          :auto-crop-height="option.autoCropHeight"
          :center-box="option.centerBox"
          :high="option.high"
          mode="contain"
          :max-img-size="option.max"
          @crop-moving="cropMoving"
          @img-load="imgLoad"
          @realTime="realTime"
        >
        </VueCropper>
      </div>
      <div class="col-2 my-auto">
        <button class="btn btn-primary" type="button" @click="getCropImgUrl"></button>
      </div>
      <div class="col-5 m-auto">
        <img v-if="cropImgUrl" :src="cropImgUrl" alt="">
      </div>
    </div>
</template>

<script>
import { VueCropper } from 'vue-cropper'
import 'vue-cropper/dist/index.css'
export default {

  components: {
    VueCropper
  },

  props: {
    imgUrl: {
      type: String
    }
  },

  data () {
    return {
      option: {
        img: '',
        size: 1,
        full: false, //* 
        outputType: 'jpg', //* 
        canMove: false, //* 
        fixedBox: false, //* 
        original: false, //* 
        canMoveBox: true, //* 
        autoCrop: true, //* 
        //*  
        autoCropWidth: 160, //* 
        autoCropHeight: 150, //* 
        centerBox: true, //* 
        high: true,
        max: 99999,
        fixed: false //* 
        // fixedNumber: [1, 1] //* 
      },
      cropImgUrl: ''
    }
  },

  methods: {
    //* 
    getCropImgUrl () {
      this.$nextTick(() => {
        this.$refs.cropper.getCropData(cropImgUrl => {
          this.cropImgUrl = cropImgUrl
          this.$parent.$refs.confirmCrop.classList.remove('d-none')
        })
      })
    },
    //* 
    getCropUrl () {
      return this.cropImgUrl
    }
  }

}
</script>

<style lang='scss' scope>
.height {
  height: 300px;
}
</style>
