<template>
    <h3></h3>
    <select v-model="freeCropMode">
      <option :value="true"></option>
      <option :value="false"></option>
    </select>
    <div class="my-3" v-if="!freeCropMode">
      <p class="fs-5"></p>
      <label for="width"></label>
      <input id="width" type="number" v-model="crop.width"
      placeholder="" style="width:120px"
      oninput="value=value.replace(/[^\d]/g,'')">
      <br />
      <label for="height"></label>
      <input id="height" type="number" v-model="crop.height" placeholder="" style="width:120px" oninput="value=value.replace(/[^\d]/g,'')">
    </div>
</template>

<script>

export default {

  watch: {
    crop: {
      handler () {
        this.$parent.$refs.cropImg.option.autoCropWidth = this.crop.width
        this.$parent.$refs.cropImg.option.autoCropHeight = this.crop.height
      },
      deep: true
    },
    freeCropMode () {
      this.$parent.$refs.cropImg.option.fixedBox = Boolean(!this.freeCropMode)
    }
  },

  data () {
    return {
      crop: {
        width: 160,
        height: 150
      },
      freeCropMode: true
    }
  }

}
</script>
